<template>
<div>
  <div class="home-title">
    <span @click="goBack"><a-icon type="arrow-left" /></span>
    <span>产品详情</span>
  </div>
  <div class="table-content">
    <div class="table-detail">
      <div class="detail-title">
        <div class="titles">{{productInfo.productLabel}}</div>
        <a-button class="other-btn" @click="showModal">更新产品信息</a-button>
      </div>
      <a-descriptions :column="4" layout="vertical">
        <a-descriptions-item label="产品名称">
          <span class="description-text"> {{productInfo.productLabel}}</span>
        </a-descriptions-item>
        <a-descriptions-item label="产品ID">
          <span class="description-text">  {{productInfo.id}}</span>
        </a-descriptions-item>
        <a-descriptions-item label="数据格式">
          <span v-if="productInfo.dataFormat === 'BINARY'">二进制码流</span>
          <span v-else>JSON</span>
        </a-descriptions-item>
        <a-descriptions-item label="产品描述">
          <span class="description-text"> {{productInfo.description}}</span>
        </a-descriptions-item>
        <a-descriptions-item label="协议类型">
          <span class="description-text">{{productInfo.protocolType}}</span>
        </a-descriptions-item>
        <a-descriptions-item label="设备类型">
          <span class="description-text">  {{productInfo.deviceType}}</span>
        </a-descriptions-item>
        <a-descriptions-item label="创建时间">
          <span class="description-text">  {{productInfo.createTime}}</span>
        </a-descriptions-item>
      </a-descriptions>
    </div>
  </div>
  <div class="table-content">
    <div class="table-tab">
      <a-tabs v-model="tabIndex" @change="callback" destroyInactiveTabPane>
        <a-tab-pane key="1" tab="模型定义">
          <modelsService :productId="productId" @isDetale="getUpdate"></modelsService>
        </a-tab-pane>
        <a-tab-pane key="2" tab="在线调试">
          <onlineDebug :productId="productId"></onlineDebug>
        </a-tab-pane>
        <a-tab-pane key="3" tab="系统预置Topic">
          <TopicList></TopicList>
        </a-tab-pane>
         <!--    产品的数据格式为二进制码流时有插件开发   v-if="productInfo.dataFormat === 'BINARY'" -->
        <a-tab-pane key="4" tab="插件开发" v-if="productInfo.dataFormat === 'BINARY'">
          <plugIn :productId="productId"></plugIn>
        </a-tab-pane>
      </a-tabs>
    </div>
  </div>
<!-- 更新产品信息弹框  -->
  <a-modal v-model="visible" :confirm-loading="confirmLoading" title="更新产品信息" @ok="handleOk" @cancel="closeHandle">
    <a-form-model :model="form" ref="productForm" :rules="formRules" :label-col="labelCol" :wrapper-col="wrapperCol">
            <a-form-model-item label="产品名称" prop='productLabel'>
              <a-input v-model="form.productLabel" placeholder="请输入产品名称"/>
            </a-form-model-item>
            <a-form-model-item label="产品描述">
              <BTextarea
                class="full-width"
                style="height: auto"
                v-model="form.description"
                placeholder="请输入备注"
              />
            </a-form-model-item>
    </a-form-model>
  </a-modal>
</div>
</template>

<script>
import TopicList from './Details/Topic/TopicList'
import onlineDebug from './Details/OnlineDebug/onlineDebug'
import BTextarea from '../../components/BTextarea/BTextarea'
import modelsService from './Details/Models/modelsService'
import plugIn from './Details/PlugIn/plugIn'
export default {
  name: 'productDetails',
  components: {
    TopicList,
    onlineDebug,
    BTextarea,
    modelsService,
    plugIn
  },
  data () {
    return {
      tabIndex: '1',
      productId: '',
      confirmLoading: false,
      visible: false,
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
      form: {
        productLabel: '',
        description: '',
        id: ''
      },
      formRules: {
        productLabel: [
          {required: true, message: '请输入设备名称', trigger: 'blur'}
        ],
        description: [
          { validator: this.$validator.checkRemarkLength, message: '描述最多128字符', trigger: ['blur', 'change'] }
        ]
      },
      productInfo: {},
      productModel: []
    }
  },
  activated() {
    this.productId = this.$route.query.id
    console.log('产品ID', this.$route.query)
    if (this.productId) {
      this.getDetails()
    }
  },
  created() {
    this.productId = this.$route.query.id
    console.log('产品ID', this.$route.query)
    if (this.productId) {
      this.getDetails()
    }
  },
  methods: {
    getUpdate() {
      this.getDetails()
    },
    /**
     * 获取产品详情
     * */
    getDetails() {
      this.$api.getProductDetails(this.productId).then(res => {
        console.log('产品详情res', res)
        this.productInfo = res.data
        this.productModel = res.data.models
        Object.assign(this.productInfo, {
          createTime: this.$getDate({timestamp: res.data.createTime})
        })
        Object.assign(this.form, {
          productLabel: res.data.productLabel,
          description: res.data.description ? res.data.description : '',
          id: res.data.id
        })
      })
    },
    goBack() {
      this.$router.push('/product')
    },
    showModal() {
      this.visible = true
      this.form = Object.assign({}, this.productInfo)
    },
    handleOk(e) {
      this.confirmLoading = true
      this.$refs.productForm.validate(valid => {
        if (valid) {
          this.$api.putProductInfo(this.form).then(res => {
            this.visible = false
            this.confirmLoading = false
            this.getDetails()
          })
        }
      })
    },
    closeHandle() {
      this.visible = false
      this.$refs['productForm'].resetFields()
    },
    callback(key) {
      console.log(key)
    }
  }
}
</script>

<style lang="scss" scoped>
.table-detail {
  padding: 24px;
  .detail-title {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px dashed #e0e4e7;
    margin-bottom: 15px;
    .titles {
      color: #333333;
      font-size: 22px;
      margin-bottom: 15px;
    }
  }
}
.description-text {
  word-break:normal;
  width:auto;
  display:block;
  white-space:pre-wrap;
  word-wrap: break-word;
  overflow: hidden ;
}
.table-tab {
  margin: 24px;
  background: #ffffff;
}
>>> .ant-tabs-bar {
  height: 70px;
  line-height: 70px;
}
>>> .ant-tabs-nav-wrap {
  height: 70px;
  line-height: 46px;
}
</style>



