<template>
<div>
<!-- 这是插件开发 -->
  <div class="code-edit">
    <div class="plug-title">
      <span>插件脚本</span></div>
    <div>
      <CodeEdit
        v-model="code"
        :defaultValue="code"
        ref="codeEdit"
        cmMode="javascript"
      />
    </div>
  </div>
  <div class="code-content">
    <div class="code-flex">
      <div class="code-left">
        <div class="left-top">
          <div class="left-title">
            <span>模型类型:</span>
            <a-select v-model="codeChoose" style="width: 200px">
              <a-select-option value="deCode">
                解码
              </a-select-option>
              <a-select-option value="inCode">
                编码
              </a-select-option>
            </a-select>
          </div>
          <div class="left-title" v-if="codeChoose === 'deCode'">
            <span>Topic:</span>
            <a-select v-model="sectOption" style="width: 500px">
              <a-select-option :value="item.value" v-for="(item, index) in deCodeOption" v-if="codeChoose === 'deCode'" :key="index">
                {{item.label}}
              </a-select-option>
              <a-select-option :value="item.value" v-for="(item, index) in inCodeOption" v-if="codeChoose === 'inCode'" :key="index">
                {{item.label}}
              </a-select-option>
            </a-select>
          </div>
        </div>
        <div class="left-border">
          <a-textarea v-model="inputVal" :placeholder="placeholder"
                      :auto-size="{ minRows: 10 }"
          />
        </div>
      </div>
      <div class="code-right">
        <span>调试结果</span>
        <div class="left-border">
          <a-textarea disabled v-model="resultVal"
                      :auto-size="{ minRows: 10 }"
          />
        </div>
      </div>
    </div>
  </div>
  <div class="code-btn">
    <a-button @click="debugPlugin">调试</a-button>
    <a-button type="primary" style="margin: 0 20px" @click="addPlugin">保存</a-button>
<!--    <a-button>部署</a-button>-->
  </div>
</div>
</template>

<script>
import CodeEdit from '../../../../components/CodeEdit/CodeEdit'
export default {
  name: 'plugIn',
  props: ['productId'],
  components: {
    CodeEdit
  },
  data () {
    return {
      inputVal: '',
      resultVal: '',
      placeholder: '输入HEX字符，如二进制[0x00, 0x50, 0x00, 0x5a]对应的模拟输入数据为0050005a',
      codeChoose: 'deCode',
      sectOption: '$oc/ota/request',
      code: 'function decode(payload, topic) {\n' +
        '    var jsonObj = {};\n' +
        '    return JSON.stringify(jsonObj);\n' +
        '}\n' +
        'function encode(json) {\n' +
        '    var payload = [];\n' +
        '    return payload;\n' +
        '}',
      inCodeOption: [
        {
          label: '$oc/ota/request',
          value: '$oc/ota/request'
        },
        {
          label: '$oc/properties/get',
          value: '$oc/properties/get'
        },
        {
          label: '$oc/config/request',
          value: '$oc/config/request'
        },
        {
          label: '$oc/config/request',
          value: '$oc/config/request'
        }
      ],
      deCodeOption: [
        {
          label: '$oc/ota/response',
          value: '$oc/ota/response'
        },
        {
          label: '$oc/properties/up',
          value: '$oc/properties/up'
        },
        {
          label: '$oc/messages/up',
          value: '$oc/messages/up'
        },
        {
          label: '$oc/config/response',
          value: '$oc/config/response'
        },
        {
          label: '$oc/control/request',
          value: '$oc/control/request'
        },
        {
          label: '$oc/control/response',
          value: '$oc/control/response'
        },
        {
          label: '$oc/warn/self',
          value: '$oc/warn/self'
        }
      ]
    }
  },
  watch: {
    'codeChoose': {
      handler() {
        console.log('监听', this.codeChoose)
        if (this.codeChoose === 'deCode') {
          this.placeholder = '输入HEX字符，如二进制[0x00, 0x50, 0x00, 0x5a]对应的模拟输入数据为0050005a'
        } else {
          this.placeholder = "输入JSON字符串，如:{'msg_type':'commands','command_name':'SET_ALARM','service_id':'smokerdector','paras':{'value':'1'}}"
        }
      }
    }
  },
  created() {
    console.log('id', this.productId)
    this.getList()
    this.getText()
  },
  methods: {
    getText() {
      if (this.codeChoose === 'deCode') {
        this.placeholder = '输入HEX字符，如二进制[0x00, 0x50, 0x00, 0x5a]对应的模拟输入数据为0050005a'
      } else {
        this.placeholder = "输入JSON字符串，如:{'msg_type':'commands','command_name':'SET_ALARM','service_id':'smokerdector','paras':{'value':'1'}}"
      }
    },
    getList() {
      const obj = {
        productId: this.productId
      }
      this.$api.getProductPluginList(obj).then(res => {
        console.log('获取插件数据', res)
        if (res.data.jsScriptBody) {
          this.code = res.data.jsScriptBody
        }
      })
    },
    /**
     * 新增js
     * */
    addPlugin() {
      this.code = this.$refs.codeEdit.getValue()
      const paramObj = {
        jsScriptBody: this.code,
        productId: this.productId,
        deployment: 'RELEASE'
      }
      this.$api.postProductPlugin(paramObj).then(res => {
        this.getList()
      })
    },
    /**
     * 调试debug
     * */
    debugPlugin() {
      this.code = this.$refs.codeEdit.getValue()
      let decode = ''
      if (this.codeChoose === 'deCode') {
        decode = true
      } else {
        decode = false
      }
      const paramObj = {
        decode: decode,
        jsScriptBody: this.code,
        input: this.inputVal,
        topic: this.sectOption
      }
      this.$api.postDebugPlugin(paramObj).then(res => {
        if (res.errorCode !== 0) {
          this.resultVal = res.msg
        } else {
          this.resultVal = res.data
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.plug-title {
  width: 100%;
  height: 45px;
  line-height: 45px;
  background: #eef0f5;
  color: #333333;
  span {
    padding-left: 20px;
  }
}
.code-content {
  width: 100%;
  height: 320px;
  background: #eef0f5;
  padding: 10px 20px;
  .code-flex {
    display: flex;
    justify-content: space-between;
    .code-left {
      flex: 1;
      margin-right: 10px;
      .left-top {
        display: flex;
        margin-bottom: 10px;
        .left-title {
          margin-right: 20px;
        }
      }
    }
    .code-right {
      flex: 1;
      span {
        line-height: 42px;
      }
    }
    .left-border {
      width: 100%;
      height: 250px;
      //border: 1px solid #333333;
    }
  }
}
.code-btn {
  margin: 10px 0;
}
>>> .ant-input[disabled] {
  color: #333333;
  background: #ffffff;
}
</style>



