<template>
<div>
  <p>
    使用MQTT协议通信的设备，通过Topic将设备数据分类上报和接收。
  </p>
  <a-table :columns="columns" :data-source="data" bordered>
    <template slot="name" slot-scope="text">
      <a>{{ text }}</a>
    </template>
  </a-table>
</div>
</template>

<script>
const renderContent = (value, row, index) => {
  const obj = {
    children: value,
    attrs: {}
  }
  return obj
}
const data = [
  {
    key: '1',
    topicName: 'OTA',
    topic: '$oc/ota/response',
    publisher: '设备',
    receiver: '服务端',
    effect: '设备OTA升级反馈，或主动请求OTA升级'
  },
  {
    key: '2',
    topicName: 'OTA',
    topic: '$oc/ota/request',
    publisher: '服务端',
    receiver: '设备',
    effect: '服务端发送OTA 内容'
  },
  {
    key: '3',
    topicName: '属性',
    topic: '$oc/properties/up',
    publisher: '设备',
    receiver: '服务端',
    effect: '设备主动上报属性'
  },
  {
    key: '4',
    topicName: '属性',
    topic: '$oc/properties/get',
    publisher: '服务端',
    receiver: '设备',
    effect: '服务端要求设备上报属性'
  },
  {
    key: '5',
    topicName: '消息',
    topic: '$oc/messages/up',
    publisher: '设备',
    receiver: '服务端',
    effect: '设备上报原始消息到服务端，可能是json,二进制，服务端会自动封装成json 并将payload内容放到content 字段'
  },
  {
    key: '6',
    topicName: '配置',
    topic: '$oc/config/request',
    publisher: '服务端',
    receiver: '设备',
    effect: '服务端主动发起修改设备基础配置，负载里面是连接信息'
  },
  {
    key: '7',
    topicName: '配置',
    topic: '$oc/config/response',
    publisher: '设备',
    receiver: '服务端',
    effect: '设备响应设置结果'
  },
  {
    key: '8',
    topicName: '反向控制',
    topic: '$oc/control/request',
    publisher: '设备/服务端',
    receiver: '服务端/设备',
    effect: '反向控制请求'
  },
  {
    key: '9',
    topicName: '反向控制',
    topic: '$oc/control/response',
    publisher: '服务端/设备',
    receiver: '设备/服务端',
    effect: '反向控制响应'
  },
  {
    key: '10',
    topicName: '预警',
    topic: '$oc/warn/self',
    publisher: '设备/服务端',
    receiver: '服务端/设备',
    effect: '告知设备或者服务端 处于预警状态，预警状态定义为因自身原因无法正常工作，比如设备电量即将耗尽，温度太高，消息体暂未做定义'
  }
]

export default {
  name: 'TopicList',
  data() {
    const columns = [
      {
        title: 'Topic分类',
        dataIndex: 'topicName',
        customRender: (value, row, index) => {
          const obj = {
            children: value,
            attrs: {}
          }
          if (index === 0) {
            obj.attrs.rowSpan = 2
          }
          if (index === 1) {
            obj.attrs.rowSpan = 0
          }
          if (index === 2) {
            obj.attrs.rowSpan = 2
          }
          if (index === 3) {
            obj.attrs.rowSpan = 0
          }
          if (index === 5) {
            obj.attrs.rowSpan = 2
          }
          if (index === 6) {
            obj.attrs.rowSpan = 0
          }
          if (index === 7) {
            obj.attrs.rowSpan = 2
          }
          if (index === 8) {
            obj.attrs.rowSpan = 0
          }
          return obj
        }
      },
      {
        title: 'topic',
        dataIndex: 'topic',
        customRender: renderContent
      },
      {
        title: '发布者',
        dataIndex: 'publisher',
        customRender: renderContent
      },
      {
        title: '接收者',
        dataIndex: 'receiver',
        customRender: renderContent
      },
      {
        title: '作用',
        dataIndex: 'effect',
        customRender: renderContent
      }
    ]
    return {
      data,
      columns
    }
  }
}
</script>

<style scoped>

</style>



