              <a-icon class="table-btn" type="plus" @click="addModel"/>
              <a-icon class="table-btn" @click="refresh" :type="reloadType" :spinning="refreshing"/>
          </span>
        </div>
        <div class="models-list">
          <ul>
            <template v-for="(item, index) in productModel">
              <li :class="{'li-active': index === currentTable, 'click_oonline': index === 0}"
                  :key="index"
                  v-trigger="index"
                  @click="modelClick(item, index)">{{item.modelLabel}}</li>
            </template>
          </ul>
        </div>
      </div>
    </a-col>
    <a-col :span="1" style="text-align: center;width: 40px">
        <a-divider type="vertical" />
    </a-col>
    <a-col :span="17">
      <div class="models-info">
        <div class="service-info">
          <div class="info-left">
