            <span class="info-font">{{serverObj.id}}</span>
            </span>
            <span class="info-title">模型名称:
            <span class="info-font">{{serverObj.modelLabel}}</span>
            </span>
            <span class="info-title">模型描述:
             <span class="info-font">{{serverObj.description}}</span>
            </span>
          </div>
          <div class="info-right">
            <a-button type="primary" style="margin-right: 16px;" @click="editModel">修改模型信息</a-button>
            <a-popconfirm
              title="你确定删除此模型吗?"
