        <div class="models-attr">
          <div class="attr-btn">
            <a-button type="primary" icon="plus" @click="showAttrModal">添加属性</a-button>
<!--            <a-button style="margin-left: 10px;" :disabled="!hasSelected" :loading="loading">批量删除</a-button>-->
          </div>
          <div>
            <a-table
