              <span slot="dataType" slot-scope="text, record">
            <span v-for="(item, index) in stateArr" v-if="item.value === text" :key="index">
              {{item.title}}
            </span>
              </span>
              <span slot="action" slot-scope="text, record">
              <span class="table-btn" @click="editPropety(record)">修改</span>
              <a-popconfirm
                title="你确定删除此属性吗?"
