                cancel-text="取消"
                @confirm="deletePropertyCancel(record.id)"
              >
              <span class="table-btn"> 删除 </span>
            </a-popconfirm>
              </span>
            </a-table>
          </div>
        </div>
        <div class="models-attr" style="margin-top: 24px;">
          <div class="attr-btn">
            <a-button type="primary" icon="plus" @click="commondModel">添加命令</a-button>
          </div>
          <div>
            <a-table
              :rowKey="(record, index) => {return record.id}"
              :pagination="false"
              :columns="commondColumns"
              :data-source="commondata">
              <span slot="action" slot-scope="text, record">
             <span class="table-btn" @click="editCommand(record)">修改</span>
                  <a-popconfirm
                    title="你确定删除此条命令吗?"
                    ok-text="确定"
                    cancel-text="取消"
                    @confirm="deleteCommand(record.id)"
                  >
              <span class="table-btn"> 删除 </span>
            </a-popconfirm>
              </span>
            </a-table>
          </div>
        </div>
      </div>
    </a-col>
  </a-row>
<!--添加属性弹框  -->
  <a-modal v-model="attrVisible" :width="700" :destroyOnClose="true" :title="propetyTitle" @ok="handleOk" @cancel="colseHandle">
    <a-form-model
      ref="propetyForm"
      :model="propetyForm"
      :rules="propetyRules"
      :label-col="labelCol"
