          placeholder="请输入描述"
        />
      </a-form-model-item>
      <a-form-model-item label="数据类型" prop="dataType">
        <a-select v-model="propetyForm.dataType" placeholder="请选择数据类型">
          <a-select-option v-for="(item, index) in stateArr" :value="item.value" :key="index">
            {{item.title}}
          </a-select-option>
        </a-select>
      </a-form-model-item>
      <a-form-model-item label="访问权限" prop="scope">
        <a-checkbox-group v-model="propetyForm.scope" :options="scopeOptions">
        </a-checkbox-group>
      </a-form-model-item>
<!--      <a-form-model-item label="取值范围" prop="minValue">
        <a-input-number placeholder="请输入最小值" v-model="propetyForm.minValue" :style="{ display: 'inline-block', width: 'calc(50% - 12px)' }"/>
        <span :style="{ display: 'inline-block', width: '24px', textAlign: 'center' }">
        -
      </span>
        <a-input-number placeholder="请输入最大值" v-model="propetyForm.maxValue" :style="{ display: 'inline-block', width: 'calc(50% - 12px)' }"/>
      </a-form-model-item>
      <a-form-model-item ref="name" label="长度" prop="propertyLength">
        <a-input v-model="propetyForm.propertyLength"/>
      </a-form-model-item>-->
    </a-form-model>
  </a-modal>
<!-- 新增命令弹框 -->
  <a-modal v-model="commondVisible" :width="900" :title="commandTitle" @ok="commondHandleOk" @cancel="closeCommand">
    <a-form-model
      ref="commandForm"
      :model="commandForm"
      :rules="commandRules"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-model-item ref="commandLabel" label="命令名称" prop="commandLabel">
        <a-input v-model="commandForm.commandLabel" placeholder="请输入命令名称"/>
      </a-form-model-item>
      <a-form-model-item label="下发参数">
       <a-button type="link" icon="plus" @click="paramModel">新增输入参数</a-button>
        <a-table
          :rowKey="(record, index) => {return index}"
          style="margin-top: 15px;"
          :columns="paraColumns"
          :data-source="parsdata">
          <span slot="dataType" slot-scope="text, record">
            <span v-for="(item, index) in stateArr" v-if="item.value === text" :key="index">
              {{item.title}}
            </span>
          </span>
              <span slot="action" slot-scope="text, record, index">
             <span class="table-btn" @click="putCommandParam(record, index)">修改</span>
             <span class="table-btn" @click="deleteCommandParams(record)">删除</span>
              </span>
        </a-table>
      </a-form-model-item>
      <a-form-model-item label="响应参数">
        <a-button type="link" icon="plus" @click="paramResModel">新增响应参数</a-button>
        <a-table
          :rowKey="(record, index) => {return index}"
          style="margin-top: 15px;"
          :columns="resParaColumns"
          :data-source="resParsdata">
           <span slot="dataType" slot-scope="text, record">
            <span v-for="(item, index) in stateArr" v-if="item.value === text" :key="index">
              {{item.title}}
            </span>
          </span>
              <span slot="action" slot-scope="text, record, index">
             <span class="table-btn" @click="putCommandResParam(record, index)">修改</span>
             <span class="table-btn" @click="deleteCommandResParams(record)">删除</span>
              </span>
        </a-table>
      </a-form-model-item>
    </a-form-model>
  </a-modal>
<!-- 新增参数弹框 -->
  <a-modal :mask="false" :maskClosable="false" :dialog-style="{ top: '220px' }"
           v-model="paramVisible"
           :width="600" :title="commandParsmsTitle"
           @cancel="paramsClose"
           @ok="paramHandleOk">
    <a-form-model
      ref="commandPropertyForm"
      :model="commandPropertyForm"
      :rules="commandPropertyRules"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-model-item ref="propertyLabel" label="参数名称" prop="paramLabel">
        <a-input v-model="commandPropertyForm.paramLabel" placeholder="请输入参数名称"/>
      </a-form-model-item>
      <a-form-model-item label="参数描述">
        <BTextarea
          class="full-width"
          style="height: auto"
          v-model="commandPropertyForm.description"
          placeholder="请输入描述"
        />
      </a-form-model-item>
      <a-form-model-item label="数据类型" prop="dataType">
        <a-select v-model="commandPropertyForm.dataType" placeholder="请选择数据类型">
          <a-select-option v-for="(item, index) in stateArr" :value="item.value" :key="index">
            {{item.title}}
          </a-select-option>
        </a-select>
      </a-form-model-item>
<!--      <a-form-model-item label="取值范围" prop="minValue">
        <a-input-number placeholder="请输入最小值" v-model="commandPropertyForm.minValue" :style="{ display: 'inline-block', width: 'calc(50% - 12px)' }"/>
        <span :style="{ display: 'inline-block', width: '24px', textAlign: 'center' }">
        -
      </span>
        <a-input-number placeholder="请输入最大值" v-model="commandPropertyForm.maxValue" :style="{ display: 'inline-block', width: 'calc(50% - 12px)' }"/>
      </a-form-model-item>-->
    </a-form-model>
  </a-modal>
<!-- 新增、修改模型弹窗 -->
  <a-modal :mask="false" :maskClosable="false" v-model="modelVisible" :width="600" :title="modelTitle"
           @cancel="closeModel"
           @ok="modelHandleOk">
    <a-form-model
      ref="modelForm"
      :model="modelForm"
      :rules="modelRules"
      :label-col="labelCol"
      :wrapper-col="wrapperCol"
    >
      <a-form-model-item ref="modelLabel" label="模型名称" prop="modelLabel">
        <a-input v-model="modelForm.modelLabel" placeholder="请输入模型名称"/>
      </a-form-model-item>
      <a-form-model-item ref="modelType" label="模型类型">
        <a-input v-model="modelForm.modelType" placeholder="请输入模型类型"/>
      </a-form-model-item>
      <a-form-model-item label="模型描述">
        <BTextarea
          class="full-width"
          style="height: auto"
          v-model="modelForm.description"
          placeholder="请输入描述"
        />
      </a-form-model-item>
    </a-form-model>
  </a-modal>
</div>
</template>

<script>
import BTextarea from '../../../../components/BTextarea/BTextarea'
const columns = [
  {
    title: '属性名称',
    ellipsis: true,
    dataIndex: 'propertyLabel'
  },
  {
    title: '数据类型',
    ellipsis: true,
    dataIndex: 'dataType',
    scopedSlots: { customRender: 'dataType' }
  },
  {
    title: '描述',
    ellipsis: true,
    dataIndex: 'description'
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
const commondColumns = [
  {
    title: '命令名称',
    ellipsis: true,
    dataIndex: 'commandLabel'
  },
  {
    title: '下发参数',
    ellipsis: true,
    dataIndex: 'paramLabel'
  },
  {
    title: '响应参数',
    ellipsis: true,
    dataIndex: 'resParamLabel'
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
const paraColumns = [
  {
    title: '参数名称',
    dataIndex: 'paramLabel'
  },
  {
    title: '数据类型',
    dataIndex: 'dataType',
    scopedSlots: { customRender: 'dataType' }
  },
  {
    title: '描述',
    dataIndex: 'description'
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
const resParaColumns = [
  {
    title: '参数名称',
    dataIndex: 'paramLabel'
  },
  {
    title: '数据类型',
    dataIndex: 'dataType',
    scopedSlots: { customRender: 'dataType' }
  },
  {
    title: '描述',
    dataIndex: 'description'
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]
export default {
  name: 'modelsService',
  props: ['productId'],
  components: {
    BTextarea
  },
  data () {
    return {
      reloadType: 'reload',
      refreshing: false,
      currentTable: 0,
      paraColumns,
      parsdata: [],
      resParsdata: [],
      commondColumns,
      resParaColumns,
      commondata: [],
      productModel: [],
      data: [],
      columns,
      selectedRowKeys: [],
      loading: false,
      pagination: {
        showLessItems: true,
        pageSizeOptions: ['10', '20', '30'],
        showSizeChanger: true,
        pageSize: 10,
        current: 1,
        hideOnSinglePage: false,
        total: 0
      },
      scopeOptions: [
        {label: '读', value: 'R'},
        {label: '写', value: 'W'}
      ],
      stateArr: [
        {
          value: 'INT',
          title: 'int(整型)'
        },
        {
          value: 'DECIMAL',
          title: 'decimal(小数)'
        },
        {
          value: 'STR',
          title: 'string(字符串)'
        },
        {
          value: 'JSON',
          title: 'jsonObject(JSON结构体)'
        },
        {
          value: 'STR_LIST',
          title: 'stringList(数组)'
        },
        {
          value: 'INT_LIST',
          title: 'intList(数组)'
        }
      ],
      attrVisible: false,
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
      other: '',
      modelForm: {
        productId: '',
        modelLabel: '',
        modelType: '',
        description: ''
      },
      modelRules: {
        modelLabel: [
          { required: true, message: '请输入模型名称', trigger: 'blur' },
          { validator: this.$validator.checkLetter, message: '模型名称只能为字母组合', trigger: ['blur', 'change'] }
        ],
        description: [
          { validator: this.$validator.checkRemarkLength, message: '描述最多128字符', trigger: ['blur', 'change'] }
        ]
      },
      commondVisible: false,
      paramVisible: false,
      modelTitle: '新增模型',
      modelVisible: false,
      serverObj: {},
      isModelEdit: false,
      isDetale: false,
      propetyForm: {
        productModelId: '',
        propertyLabel: '',
        description: '',
        dataType: undefined,
        scope: [],
        minValue: 0,
        maxValue: 0,
        propertyLength: ''
      },
      propetyRules: {
        propertyLabel: [
          { required: true, message: '请输入属性名称', trigger: 'blur' },
          { validator: this.$validator.checkLetter, message: '属性名称只能为字母组合', trigger: ['blur', 'change'] }
        ],
        dataType: [
          { required: true, message: '请选择数据类型', trigger: 'blur' }
        ],
        propertyLength: [
          { required: true, message: '请输入属性长度', trigger: 'blur' }
        ],
        scope: [
          { required: true, message: '请选择访问权限', trigger: 'blur' }
        ],
        minValue: [
          { required: true, message: '请输入值', trigger: 'blur' }
        ]
      },
      propetyTitle: '新增属性',
      isPropetyEdit: false,
      commandForm: {
        productModelId: '',
        commandLabel: '',
        params: [],
        responses: []
      },
      commandRules: {
        commandLabel: [
          { required: true, message: '请输入命令名称', trigger: 'blur' },
          { validator: this.$validator.checkLetter, message: '属性命令只能为字母组合', trigger: ['blur', 'change'] }
        ]
      },
      isEditCommand: false,
      commandTitle: '新增命令',
      commandPropertyForm: {
        paramLabel: '',
        description: '',
        dataType: undefined,
        minValue: 0,
        maxValue: 0,
        id: '',
        scope: 'RW'
      },
      commandPropertyRules: {
        paramLabel: [
          { required: true, message: '请输入参数名称', trigger: 'blur' },
          { validator: this.$validator.checkLetter, message: '参数名称只能为字母组合', trigger: ['blur', 'change'] }
        ],
        dataType: [
          { required: true, message: '请选择数据类型', trigger: 'blur' }
        ],
        minValue: [
          { required: true, message: '请输入值', trigger: 'blur' }
        ]
      },
      isResParam: false,
      commandParsmsTitle: '新增参数',
      isEditParam: false,
      editIndex: 0,
      editResIndex: 0,
      isEditResParam: false
    }
  },
  directives: {
    trigger: {
      inserted (el, binding) {
        if (binding.value !== 0) return
        if (binding.value === 0) {
          el.click()
        }
      }
    }
  },
  computed: {
    hasSelected() {
      return this.selectedRowKeys.length > 0
    }
  },
  created() {
    this.modelForm.productId = this.productId
    this.getModelsList()
    console.log('thisA', this.productId)
  },
  methods: {
    /**
     * 刷新服务列表
     * */
    refresh() {
      this.refreshing = true
      if (this.refreshing) {
        this.reloadType = 'loading'
        setTimeout(() => {
          this.refreshing = false
          this.reloadType = 'reload'
          this.getModelsList()
        }, 2000)
      }
    },
    /**
     * 初始产品模型信息
     * */
    modelClick(item, index) {
      console.log('item', item)
      this.serverObj = item
      this.currentTable = index
      this.data = item.properties
      Object.assign(this.serverObj, {
        description: item.description ? item.description : ''
      })
      this.getPropertyList()
      this.getCommandList()
    },
    start() {
      this.loading = true
      // ajax request after empty completing
      setTimeout(() => {
        this.loading = false
        this.selectedRowKeys = []
      }, 1000)
    },
    /**
     * 添加属性
     * */
    showAttrModal() {
      this.attrVisible = true
      this.isPropetyEdit = false
      this.propetyForm = {}
    },
    editPropety(val) {
      console.log('0000', val)
      this.propetyTitle = '修改属性'
      this.attrVisible = true
      this.isPropetyEdit = true
      val.scope = val.scope instanceof Array ? val.scope : val.scope.split('')
      console.log('属性', val)
      this.propetyForm = Object.assign({}, val)
    },
    handleOk() {
      if (this.isPropetyEdit) {
        this.putPropety()
      } else {
        this.postAddPropety()
      }
      this.getPropertyList()
    },
    colseHandle() {
      this.attrVisible = false
      this.$refs['propetyForm'].resetFields()
    },
    postAddPropety() {
      this.propetyForm.productModelId = this.serverObj.id
      this.$refs.propetyForm.validate(valid => {
        if (valid) {
          if (this.propetyForm.scope.length > 1) {
            this.propetyForm.scope = this.propetyForm.scope.toString().replace(/,/g, '')
          } else {
            this.propetyForm.scope = this.propetyForm.scope.toString()
          }
          this.$api.postProductModelProperty(this.propetyForm).then(res => {
            this.attrVisible = false
            this.getPropertyList()
          })
        }
      })
    },
    putPropety() {
      this.propetyForm.productModelId = this.serverObj.id
      this.$refs.propetyForm.validate(valid => {
        if (valid) {
          if (this.propetyForm.scope.length > 1) {
            this.propetyForm.scope = this.propetyForm.scope.toString().replace(/,/g, '')
          } else {
            this.propetyForm.scope = this.propetyForm.scope.toString()
          }
          this.$api.putProductModelProperty(this.propetyForm).then(res => {
            this.attrVisible = false
            this.getPropertyList()
          })
        }
      })
    },
    /**
     * 属性列表
     * */
    getPropertyList() {
      const obj = {
        modelId: this.serverObj.id
      }
      this.$api.getProductModelProperty(obj).then(res => {
        this.data = res.data
        this.data.map(item => {
          Object.assign(item, {
            description: item.description ? item.description : ''
          })
        })
      })
    },
    /**
     * 删除属性
     * */
    deletePropertyCancel(id) {
      this.$api.deleteProductModelProperty(id).then(res => {
        this.getPropertyList()
      })
    },
    /**
     * 新增命令
     * */
    commondModel() {
      this.commondVisible = true
      this.isEditCommand = false
      this.commandForm = {}
      console.log('THIS4', this)
    },
    commondHandleOk() {
      if (this.isEditCommand) {
        this.putCommand()
      } else {
        this.postCommand()
      }
      this.getCommandList()
    },
    closeCommand() {
      this.commondVisible = false
      this.$refs['commandForm'].resetFields()
    },
    editCommand(val) {
      console.log('修改的命令', val)
      this.commondVisible = true
      this.commandTitle = '修改命令'
      this.isEditCommand = true
      val.params.map(item => {
        Object.assign(item, {
          scope: 'RW'
        })
      })
      val.responses.map(item => {
        Object.assign(item, {
          scope: 'RW'
        })
      })
      this.parsdata = val.params
      this.resParsdata = val.responses
      this.resParsdata.map((item, index) => {
        Object.assign(item, {
          id: item.paramLabel + index
        })
      })
      this.parsdata.map((item, index) => {
        Object.assign(item, {
          id: item.paramLabel + index
        })
      })
      this.commandForm = Object.assign({}, val)
    },
    postCommand() {
      this.commandForm.productModelId = this.serverObj.id
      this.commandForm.params = this.parsdata
      this.commandForm.responses = this.resParsdata
      this.$refs.commandForm.validate(valid => {
        if (valid) {
          this.$api.postProductModelCommand(this.commandForm).then(res => {
            this.commondVisible = false
            this.getCommandList()
          })
        }
      })
    },
    putCommand() {
      this.commandForm.productModelId = this.serverObj.id
      this.$refs.commandForm.validate(valid => {
        if (valid) {
          this.$api.putProductModelCommand(this.commandForm).then(res => {
            this.commondVisible = false
            this.getCommandList()
          })
        }
      })
    },
    /**
     * 命令列表
     * */
    getCommandList() {
      const obj = {
        modelId: this.serverObj.id
      }
      this.$api.getProductModelCommand(obj).then(res => {
        console.log('获取命令列表', res)
        this.commondata = res.data
        if (this.commondata.length > 0) {
          this.commondata.map(item => {
            if (item.params.length > 0) {
              let paramLables = []
              let paramsArr = item.params
              paramsArr.map(item => {
                paramLables.push(item.paramLabel)
              })
              Object.assign(item, {
                paramLabel: paramLables.join(',')
              })
            }
            if (item.responses.length > 0) {
              let resparamLables = []
              let resparamsArr = item.responses
              resparamsArr.map(item => {
                resparamLables.push(item.paramLabel)
              })
              Object.assign(item, {
                resParamLabel: resparamLables.join(',')
              })
            }
          })
        }
      })
    },
    /**
     * 命令删除
     * */
    deleteCommand(id) {
      this.$api.deleteProductModelCommand(id).then(res => {
        this.getCommandList()
      })
    },
    /**
     * 新增命令参数
     * */
    paramModel() {
      this.paramVisible = true
      this.isResParam = false
      this.commandPropertyForm = {}
      this.isEditParam = false
    },
    paramHandleOk() {
      console.log('还是这个？')
      this.$refs.commandPropertyForm.validate(valid => {
        if (valid) {
          let paramObj = {}
          if (this.isResParam) {
            if (this.isEditResParam) {
              this.resParsdata.map((item, index) => {
                if (this.editIndex === index) {
                  Object.assign(item, this.commandPropertyForm)
                  this.paramVisible = false
                }
              })
            } else {
              paramObj = this.commandPropertyForm
              this.resParsdata.push(paramObj)
              this.resParsdata.map((item, index) => {
                Object.assign(item, {
                  id: item.paramLabel + index
                })
              })
              this.paramVisible = false
            }
          } else {
            if (this.isEditParam) {
              this.parsdata.map((item, index) => {
                if (this.editIndex === index) {
                  Object.assign(item, this.commandPropertyForm)
                  console.log('我修改的数据', item)
                  this.paramVisible = false
                }
              })
            } else {
              console.log('this', this)
              paramObj = this.commandPropertyForm
              this.parsdata.push(paramObj)
              this.parsdata.map((item, index) => {
                Object.assign(item, {
                  id: item.paramLabel + index
                })
              })
              console.log('下发参数', this.parsdata)
              this.paramVisible = false
            }
          }
        }
      })
    },
    paramsClose() {
      this.paramVisible = false
      this.$refs['commandPropertyForm'].resetFields()
    },
    paramResModel() {
      this.paramVisible = true
      this.isResParam = true
      this.isEditResParam = false
      this.commandPropertyForm = {}
    },
    putCommandParam(item, index) {
      console.log('修改的idnex', index)
      this.paramVisible = true
      this.isResParam = false
      this.isEditParam = true
      this.commandParsmsTitle = '修改参数'
      this.editIndex = index
      item.scope = 'RW'
      this.commandPropertyForm = Object.assign({}, item)
    },
    /**
     * 下发参数删除
     * */
    deleteCommandParams(val) {
      console.log('this.parsdata', this.parsdata)
      console.log('删除的', val)
      this.parsdata.map((item, index) => {
        if (val.id === item.id) {
          console.log('删除了', item)
          this.parsdata.splice(index, 1)
        }
      })
    },
    putCommandResParam(item, index) {
      this.paramVisible = true
      this.isResParam = true
      this.isEditResParam = true
      this.commandParsmsTitle = '修改参数'
      this.editResIndex = index
      item.scope = 'RW'
      this.commandPropertyForm = Object.assign({}, item)
    },
    /**
     * 响应参数删除
     * */
    deleteCommandResParams(val) {
      this.resParsdata.map((item, index) => {
        if (val.id === item.id) {
          this.resParsdata.splice(index, 1)
        }
      })
    },
    /**
     * 新增模型
     * */
    addModel() {
      this.modelVisible = true
      this.isModelEdit = false
      this.modelForm = {}
    },
    modelHandleOk() {
      if (this.isModelEdit) {
        this.putModels()
      } else {
        this.postAddModels()
      }
    },
    closeModel() {
      this.modelVisible = false
      this.$refs['modelForm'].resetFields()
    },
    postAddModels() {
      this.modelForm.productId = this.productId
      this.$refs.modelForm.validate(valid => {
        if (valid) {
          this.$api.postProductModels(this.modelForm).then(res => {
            this.modelVisible = false
            this.getModelsList()
          })
        }
      })
    },
    putModels() {
      this.modelForm.productId = this.productId
      this.$refs.modelForm.validate(valid => {
        if (valid) {
          this.$api.putProductModels(this.modelForm).then(res => {
            this.modelVisible = false
            this.getModelsList()
          })
        }
      })
    },
    /**
     * 修改模型信息
     * */
    editModel() {
      this.modelTitle = '修改模型信息'
      this.modelVisible = true
      this.isModelEdit = true
      Object.assign(this.modelForm, {
        description: this.serverObj.description,
        modelLabel: this.serverObj.modelLabel,
        modelType: this.serverObj.modelType,
        productId: this.productId,
        id: this.serverObj.id
      })
      // this.modelForm = this.serverObj
    },
    /**
     * 删除模型
     * */
    deleteModelCancel(id) {
      this.$api.deleteProductModels(id).then(res => {
        this.isDetale = res.data
        this.$emit('isDetale', this.isDetale)
        this.getModelsList()
      })
    },
    /**
     * 模型列表
     * */
    getModelsList() {
      const obj = {
        productId: this.productId
      }
      this.$api.getProductModelList(obj).then(res => {
        this.productModel = res.data
        this.productModel.map(item => {
          if (item.id === this.serverObj.id) {
            console.log('888', item)
            this.serverObj = item
            Object.assign(this.serverObj, {
              description: item.description ? item.description : ''
            })
          }
        })
        console.log('修改的obj', this.serverObj)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
 .models-content {
   width: 100%;
   //border-right: 1px solid #e0e4e7;
   min-height: 44rem;
   margin-top: 10px;
   .models-list-title {
     display: flex;
     justify-content: space-between;
     border-bottom: 1px solid #e0e4e7;
     padding: 20px 0 20px 10px;
     font-size: 16px;
     line-height: 10px;
     span {
       color: #333333;
     }
   }
   .models-list {
     margin-top: 5px;
     ul {
       margin: 0;
       padding: 0;
       li {
         height: 48px;
         line-height: 48px;
         color: #333333;
         padding-left: 20px;
       }
       .li-active {
         background: linear-gradient(90deg,rgba(0,98,255,0.08), rgba(16,103,238,0.08));
       }
     }
   }
 }
 .models-info {
   margin-top: 10px;
   .service-info {
     display: flex;
     justify-content: space-between;
     height: 55px;
     color: #333333;
     line-height: 55px;
     .info-left {
       .info-title {
         font-size: 14px;
         color: #6d7278;
       }
       .info-font {
         color: #333333;
         padding-right: 20px;
         padding-left: 5px;
       }
     }
   }
 }
 .models-attr {
   border: 1px solid #f0f0f0;
   .attr-btn {
      margin: 20px 20px;
   }
 }
 >>> .ant-divider, .ant-divider-vertical {
   min-height: 44rem;
 }
</style>



