<template>
<div>
  <div class="debug-content">
    <div class="debug-title">
      <a-button type="primary" @click="showModal"> <a-icon type="plus" />新增虚拟设备</a-button>
    </div>
    <div>
      <a-table :columns="columns"
               :rowKey="(record, index) => {return index}"
               :pagination="false"
               :data-source="data">
        <a slot="name" slot-scope="text">{{ text }}</a>
        <span slot="action" slot-scope="text, record">
        <span class="table-btn" @click="goDebug(record)">调试</span>
       <a-popconfirm
         title="你确定删除该设备吗?"
         ok-text="确定"
         cancel-text="取消"
         @confirm="deleteCancel(record.id)"
       >
       <span class="table-btn"> 删除 </span>
       </a-popconfirm>
    </span>
      </a-table>
    </div>
  </div>
  <a-modal v-model="visible" title="新增虚拟设备" @ok="handleOk" width="560px">
    <div class="model-box">
      <div class="img-title">设备类型：</div>
      <div class="img-box">
        <img src="../../../../assets/images/product/device.png" alt="">
        <div class="titles">虚拟设备</div>
        <p><a-icon type="bulb" style="color: #F39000;padding-right: 5px;"/>您正在注册一个虚拟设备</p>
      </div>
    </div>
<!--    <a-form-model :model="form" :rules="rules" :label-col="labelCol" :wrapper-col="wrapperCol">
      <a-form-model-item label="设备类型">
        <a-radio-group v-model="form.layout" button-style="solid">
          <a-radio-button value="1">
            真实设备
          </a-radio-button>
          <a-radio-button value="2">
            虚拟设备
          </a-radio-button>
        </a-radio-group>
      </a-form-model-item>
      <a-form-model-item label="设备名称" prop="mobile">
        <a-input v-model="form.mobile" placeholder="请输入设备名称"/>
      </a-form-model-item>
      <a-form-model-item label="设备标识码" prop="code">
        <a-input v-model="form.mobile" placeholder="请输入正确的设备标识码"/>
      </a-form-model-item>
      <a-form-model-item label="设备注册方式" prop="mobile">
        <a-radio-group v-model="form.layout" button-style="solid">
          <a-radio-button value="1">
            不加密
          </a-radio-button>
          <a-radio-button value="2">
            加密
          </a-radio-button>
        </a-radio-group>
      </a-form-model-item>
    </a-form-model>-->
  </a-modal>
</div>
</template>

<script>
const columns = [
  {
    dataIndex: 'deviceLabel',
    ellipsis: true,
    title: '设备名称',
    key: 'deviceLabel'
  },
  {
    title: '设备标识码',
    ellipsis: true,
    dataIndex: 'deviceCode',
    key: 'deviceCode'
  },
  {
    title: '设备ID',
    ellipsis: true,
    dataIndex: 'id',
    key: 'id'
  },
  {
    title: '类型',
    ellipsis: true,
    key: 'invented',
    dataIndex: 'invented'
  },
  {
    title: '操作',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  name: 'onlineDebug',
  props: ['productId'],
  data () {
    return {
      data: [],
      columns,
      pagination: {
        showLessItems: true,
        pageSizeOptions: ['10', '20', '30'],
        showSizeChanger: true,
        pageSize: 10,
        current: 1,
        hideOnSinglePage: false,
        total: 0
      },
      visible: false,
      form: {
        name: '',
        desc: '',
        code: '',
        layout: '2'
      },
      rules: {
        code: [
          {required: true, message: '请输入设备标识码', trigger: 'blur'}
        ]
      }
    }
  },
  created() {
    this.getList()
  },
  methods: {
    /**
     * 获取产品设备列表
     * */
    getList() {
      let paramsObj = {
        deviceLabel: '',
        productId: this.productId
      }
      this.$api.getProductDevice(paramsObj).then(res => {
        this.data = res.data
        console.log('虚拟设备', res.data)
      })
    },
    /**
     * 删除设备
     * */
    deleteCancel(id) {
      this.$api.deleteDevice(id).then(res => {
        this.getList()
      })
    },
    showModal() {
      this.visible = true
    },
    handleOk() {
      this.$api.postDeviceInvented(this.productId).then(res => {
        this.getList()
        this.visible = false
      })
    },
    goDebug(item) {
      let data = JSON.stringify(item)
      this.$router.push({
        path: '/online',
        query: {
          deviceObj: data
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.debug-title {
  margin-bottom: 15px;
}
.model-box {
  display: flex;
  .img-title {

  }
  .img-box {
    width: 370px;
    text-align: center;
    position: relative;
    .titles {
      color: #333333;
      font-weight: bold;
      font-size: 14px;
      position: absolute;
      left: 44%;
      bottom: 37%;
    }
    p {
      margin-top: 14px;
    }
  }
}
</style>



